<template>
  <section class="popup">
    <section class="login-popup">
      <i class="icon-close" @click="$emit('closeRegPopup')"></i>
      <div>
        <div class="form-group">
          <input placeholder="请输入昵称" class="code-input" v-model="nick_name"/>
          <a @click="setNickName">设置昵称</a>
        </div>
        <div class="form-group">
          <input placeholder="请输入喜欢的歌手" v-model="singers"/>
        </div>
        <div class="form-group-checkbox">
          <p>感兴趣歌曲类型:</p>
          <input type="checkbox" name="love_old" v-model="formData.love_old"/>老歌
          <input type="checkbox" name="love_popular" v-model="formData.love_popular"/>流行音乐
          <input type="checkbox" name="love_flok" v-model="formData.love_flok"/>民谣
          <input type="checkbox" name="love_rap" v-model="formData.love_rap"/>说唱<br>
          <input type="checkbox" name="love_dj" v-model="formData.love_dj"/>DJ
          <input type="checkbox" name="love_new" v-model="formData.love_new"/>新歌
          <input type="checkbox" name="love_classical" v-model="formData.love_classical"/>古典
        </div>
      </div>
      <div class="btn" @click="setInterest">设置兴趣爱好</div>
    </section>
  </section>
</template>

<script>
import { mapActions } from 'vuex'
export default {
  name: 'user',
  data () {
    return {
      nick_name: '',
      singers: '',
      formData: {
        love_old: '',
        love_popular: '',
        love_flok: '',
        love_rap: '',
        love_dj: '',
        love_new: '',
        love_classical: ''
      }
    }
  },
  created () {
  },
  methods: {
    // 设置昵称
    async setNickName () {
      const params = {
        nick_name: this.nick_name
      }
      const res = await this.$http.post('/user/index/alter', params)
      alert(res.msg)
      if (res.status === true) {
        const info = await this.$http.post('/user/index/index', {})
        if (info.status === true) {
          this.handleSetNickName(info.data.nick_name)
          location.reload()
        }
      }
    },
    // 设置兴趣爱好
    async setInterest () {
      for (let i in this.formData) {
        this.formData[i] = this.formData[i] === true ? 1 : 0
      }
      const params = { ...this.formData, singers: this.singers }
      const res = await this.$http.post('/user/song/interesting', params)
      alert(res.msg)
      if (res.status === true) {
        location.reload()
      }
    },
    ...mapActions('player/user', [
      'handleSetNickName'
    ])
  }
}
</script>

<style scoped lang="scss">
.popup {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba($color: #000000, $alpha: .7);
  z-index: 20;
  .login-popup {
    position: relative;
    margin: 0 auto;
    margin-top: 100px;
    width: 400px;
    height: 400px;
    background: #fff;
    overflow: hidden;
    .icon-close {
      position: absolute;
      top: 20px;
      right: 20px;
      display: inline-block;
      width: 20px;
      height: 20px;
      background: url('../assets/images/close.png');
      background-size: 100% 100%;
      color: #000;
      cursor: pointer;
    }
    .form-group-checkbox {
      p {
        margin: 0 0 10px;
        font-size: 16px;
        color: #333;
      }
      margin: 0 auto;
      margin-top: 30px;
      width: 300px;
      font-size: 14px;
      input {
        margin: 4px;
      }
    }
    .form-group {
      margin: 0 auto;
      margin-top: 30px;
      width: 300px;
      font-size: 16px;
      &:first-child {
        margin-top: 80px;
      }
      input {
        width: 288px;
        position: relative;
        height: 18px;
        padding: 10px 0 10px 10px;
        line-height: 18px;
        border: 1px solid #efefef;
        background: 0 0;
        color: #666;
        font-family: Verdana,Tahoma,Arial;
        font-size: 16px;
        &.code-input {
          width: 170px;
        }
      }
      a {
        margin: 0 0 0 10px;
        position: relative;
        height: 18px;
        padding: 10px 10px 10px 10px;
        line-height: 18px;
        background: 0 0;
        color: #999;
        font-family: Verdana,Tahoma,Arial;
        font-size: 14px;
        cursor: pointer;
      }
    }
    .btn {
      margin: 0 auto;
      margin-top: 30px;
      width: 300px;
      height: 40px;
      line-height: 35px;
      border: none;
      font-size: 17px;
      font-weight: 400;
      color: #fff;
      background: #86CE2F;
      cursor: pointer;
      text-align: center;
    }
  }
}
</style>
